import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { 
  ActionSheetController ,
  Platform ,
  AlertController
} from 'ionic-angular';

@IonicPage({
  name:'AlertPage',
  segment:'alert/:id'
})
@Component({
  selector: 'page-alert',
  templateUrl: 'alert.html'
})
export class AlertPage {

  constructor(public navCtrl: NavController,
    public actionSheetCtrl: ActionSheetController,
    public platform: Platform, public navParams: NavParams,
    public alertCtrl: AlertController) {
    console.log(this)
  }
  openAlert() {
    let alert=this.alertCtrl.create({
      title:'嗨，新朋友!',
      subTitle:'在这里向你问好',
      buttons:['确认']
    });
    alert.present();
  }
  showPrompt() {
    let prompt = this.alertCtrl.create({
      title: '登录',
      message: "请输入您的用户名",
      inputs: [
        {
          name: 'username',
          placeholder: '用户名'
        },
        {
          name: 'pwd',
          placeholder: '密码'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log(data);
            //data为对象,name即为用户输入
          }
        },
        {
          text: 'Save',
          handler: data => {
            console.log(data);
          }
        }
      ]
    });
    prompt.present();
  }
  showConfirm() {
    let confirm = this.alertCtrl.create({
      title: '使用这个亮度?',
      message: '你想调整亮度吗?',
      buttons: [
        {
          text: '不同意',
          handler: () => {
            console.log('Disagree clicked');
          }
        },
        {
          text: '同意',
          handler: () => {
            console.log('Agree clicked');
          }
        }
      ]
    });
    confirm.present();
  }
  showRadio() {
    let alert = this.alertCtrl.create();
    alert.setTitle('亮度条的颜色');

    alert.addInput({
      type: 'radio',
      label: '蓝色',
      value: 'blue',
      checked: true
    });
    alert.addInput({
      type: 'radio',
      label: '红色',
      value: 'red',
      checked: false
    });

    alert.addButton('关闭');
    alert.addButton({
      text: '确认',
      handler: data => {
        console.log(data);
        //data即为选中的值
      }
    });
    alert.present();
  }
  showCheckbox() {
    let alert = this.alertCtrl.create();
    alert.setTitle('你想访问那个面板?');

    alert.addInput({
      type: 'checkbox',
      label: '主页',
      value: 'home',
      checked: true
    });

    alert.addInput({
      type: 'checkbox',
      label: '联系方式',
      value: 'contact'
    });

    alert.addButton('关闭');
    alert.addButton({
      text: '确认',
      handler: data => {
        console.log('Checkbox data:', data);
        //data为数组
      }
    });
    alert.present();
  }
  openMenu() {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Albums',
      cssClass: 'action-sheets-basic-page',
      buttons: [
        {
          text: 'Delete',
          role: 'destructive',
          icon: !this.platform.is('ios') ? 'trash' : null,
          handler: () => {
            console.log('Delete clicked');
          }
        },
        {
          text: 'Share',
          icon: !this.platform.is('ios') ? 'share' : null,
          handler: () => {
            console.log('Share clicked');
          }
        },
        {
          text: 'Play',
          icon: !this.platform.is('ios') ? 'arrow-dropright-circle' : null,
          handler: () => {
            console.log('Play clicked');
          }
        },
        {
          text: 'Favorite',
          icon: !this.platform.is('ios') ? 'heart-outline' : null,
          handler: () => {
            console.log('Favorite clicked');
          }
        },
        {
          text: 'Cancel',
          role: 'cancel', // will always sort to be on the bottom
          icon: !this.platform.is('ios') ? 'close' : null,
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

}



